.display {
    width: 300px;
    height: 110px;
    /*background-color: #282828;*/
    border: 1px black solid;
    margin-bottom: 35px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    border-radius: 5px;
}


.displayMessageStyle {
    font-size: 20px;
}

.displayDarkMode {
    width: 300px;
    height: 110px;
    border: 1px white solid;
    margin-bottom: 35px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    border-radius: 5px;
}

.lastValueStyle {
    font-size: 70px;
    color: #DC143C;
}

@media(max-width:355px) {
    .display {
        width: 280px;
        height: 100px;
    }

    .displayDarkMode {
        width: 280px;
        height: 100px;
    }
}
